.team {
  margin-bottom: 1rem;

  &__items {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 1rem;
  }

  &__item {
    background: rgba(0, 0, 0, .5);
    text-align: center;
    padding: 0.5rem;
    // border: 2px solid red;
    border-radius: 5%;

    transition: all .3s;

    &:hover {
    background: rgba(0, 0, 0, .8);

      transform: scale(1.5);

    }

    &--name {
      color: $color3;
      font-size: 1.2rem;
      margin-bottom: 1rem;
    }

    // &--img {}
  }
}