@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  position: relative;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  min-width: 320px;
  font-size: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
body {
  background: url("../img/bg.jpg") no-repeat top center;
  background-size: cover;
  font-family: "Roboto", sans-serif;
  font-size: 2rem;
  line-height: 2.3rem;
  font-weight: 500;
  color: #fff;
}

.tabs__item {
  display: none;
}

.tabs__item--active {
  display: block;
}

.page {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  position: relative;
}

img {
  max-width: 100%;
}

a {
  color: #fff;
}

.container {
  max-width: 130rem;
  padding: 0 1rem;
  margin: 0 auto;
  position: relative;
}

.btn {
  display: inline-block;
  background: #2F3132;
  line-height: 4rem;
  color: #EDEDED;
  text-align: center;
  transition: color 0.3s;
}
.btn:hover {
  color: #fff;
}

.title {
  font-size: 3.6rem;
  line-height: 4.2rem;
  font-weight: 900;
  color: #2F3132;
  text-align: center;
}

.text {
  font-size: 2rem;
  line-height: 2.3rem;
  font-weight: normal;
  color: #2F3132;
}

.header {
  background: #c52222;
  width: 100%;
  margin-bottom: 5rem;
}
.header__logo {
  position: absolute;
  top: 0;
  width: 15%;
}
.header__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 0;
}

.menu__btn {
  display: none;
}
.menu__list {
  display: flex;
}
.menu__list-link {
  font-size: 1.2rem;
  line-height: 1.4rem;
  font-weight: 500;
  color: #fff;
  transition: all 0.3s;
  padding: 0.5rem 2rem;
  border: 1px solid #fff;
}
.menu__list-link--active {
  background: #fff;
  color: #da2525;
  border: 1px solid #EDEDED;
  box-shadow: 0.1rem 0.1rem 0.5rem #2F3132 inset;
}
.menu__list-link:hover {
  background: #fff;
  color: #da2525;
  box-shadow: 0.5rem 0.5rem 1rem #2F3132;
}
.menu__item {
  margin-right: 2rem;
}

.hero {
  padding-top: 10rem;
}
.hero__items {
  display: flex;
  justify-content: center;
  align-content: center;
  text-align: center;
}
.hero__title {
  text-shadow: 0.1rem 0.1rem 0.5rem #EDEDED;
}

.team {
  margin-bottom: 1rem;
}
.team__items {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  gap: 1rem;
}
.team__item {
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
  padding: 0.5rem;
  border-radius: 5%;
  transition: all 0.3s;
}
.team__item:hover {
  background: rgba(0, 0, 0, 0.8);
  transform: scale(1.5);
}
.team__item--name {
  color: #EDEDED;
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.table__items {
  display: flex;
  justify-content: center;
  align-content: center;
}
.table__item {
  margin: 2rem;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 1%;
}
.table__item:hover {
  transform: scale(1.5) translate(0, 30%);
}

.footer {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-top: auto;
  background: #fff;
  opacity: 0.4;
  transition: all 0.3s;
}
.footer:hover {
  opacity: 1;
}
.footer__items {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0.5rem 0;
}
.footer__item + .footer__item {
  margin-left: 1rem;
}
.footer__text {
  color: #2F3132;
  font-size: 1.4rem;
}
.footer__text:hover {
  color: #be4e1a;
}
.footer__git {
  width: 3rem;
}