.table{
  &__items{
    display: flex;
    justify-content: center;
    align-content: center;
    
  }

  &__item{
    margin: 2rem;
    padding: 2rem;
    background: rgba(0, 0, 0, .4);
    border-radius: 1%;
    
    &:hover{
      transform: scale(1.5) translate(0, 30%);
    }
  }
}